import { Link, withPrefix } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"

const brandStyles = {
  position: "absolute",
  top: `8px`,
}

const Header = ({ siteTitle, activeIndex }) => (
  <Navbar bg="dark" variant="dark" expand="lg" fixed="top" collapseOnSelect>
    <Navbar.Brand style={brandStyles} href={withPrefix("/")}>Hardstuck Iron</Navbar.Brand>
    <Navbar.Toggle className="ml-auto"/>
    <Navbar.Collapse>
      <Nav className="mx-auto">
        <Nav.Link active={activeIndex === 'home'} href={withPrefix("/")}>Home</Nav.Link>
        <Nav.Link active={activeIndex === 'summoners'}>Summoners</Nav.Link>
        <Nav.Link active={activeIndex === 'champions'} href={withPrefix("/champions/")}>Champions</Nav.Link>
        <Nav.Link active={activeIndex === 'stats'}>Stats</Nav.Link>
      </Nav>
    </Navbar.Collapse>
  </Navbar>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
  activeIndex: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
  activeIndex: `home`,
}

export default Header
